@import "./src/assets/themes/appdb/variables.scss";

.state {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;

  &__img {
    max-width: 100%;
  }

  &__heading {
    font-size: 16px;
    font-weight: 500;
    margin: 15px 0 10px;
  }

  &__status {
    font-size: 14px;
    margin-bottom: 15px;
    white-space: pre-line;
  }
}
